 {\n            background-color: $color-palette-black-op-10;\n        }\n    }\n}\n","@forward \"../shared/common\";\n@forward \"../shared/colors\";\n@forward \"../shared/fonts\";\n@forward \"../shared/shadows\";\n@forward \"../shared/spacing\";\n\n@use \"sass:math\";\n@use \"../shared/common\" as *;\n@use \"../shared/colors\" as *;\n@use \"../shared/fonts\" as *;\n@use \"../shared/shadows\" as *;\n@use \"../shared/spacing\" as *;\n\n$input-border-color-hover: $color-palette-primary;\n$input-inset-shadow: inset 0 0 0 1px $color-palette-primary, inset 0 0 0 1px $color-palette-primary,\n    inset 0 0 0 1px $color-palette-primary, inset 0 0 0 1px $color-palette-primary;\n$input-border-color: $color-palette-gray-500;\n$input-border-color-focus: $color-palette-gray-500;\n$input-bg-color: $color-palette-gray-500;\n$input-bg-color-hover: $color-palette-gray-500;\n\n$bg-highlight: $color-palette-primary-op-10;\n$text-color-highlight: $color-palette-primary;\n$border-color-highlight: $color-palette-primary-op-10;\n\n$focus-bg: $color-palette-primary-op-10;\n$focus-text-color: $black;\n\n$bg-hover: $color-palette-gray-200;\n$text-color-hover: $black;\n$border-color-hover: $color-palette-primary;\n\n$button-text-color-disabled: $color-palette-gray-500;\n$button-text-color: $color-palette-gray-500;\n$button-shadow-focus: 0 0 0 4px $color-palette-secondary-op-20;\n\n// Tables\n$th-text-color: $color-palette-gray-500;\n$table-border-color: $color-palette-gray-500;\n\n$warn: $orange;\n$info: $color-palette-secondary;\n\n// Basic stuffs\n$field-tiny-height: 28px;\n$field-disabled-opacity: 0.7;\n$line-height: 1.5em;\n\n// ICONS\n$md-icon-size-normal: 18px;\n$md-icon-size-tiny: 14px;\n$md-icon-size-big: 24px;\n$md-icon-size-extra-big: 2rem;\n\n// ANIMATION\n$basic-speed: 150ms;\n\n// LAYOUT\n$toolbar-height: 60px;\n$toolbar-background: $white;\n$toolbar-color: $black;\n$navigation-item-height: 48px;\n$navigation-width: 200px;\n$navigation-width-collased: 70px;\n$content-palette-widt